import React from "react";
import styled from "styled-components";
import {NavLink, Outlet} from "react-router-dom";
import {Button} from "@mui/material";

export const NavMenu = () => {
    return (<>



            <Projects>


                <Button><StyledNavLink to="/useparamsapp">UseParamsApp</StyledNavLink></Button>
                <Button><StyledNavLink to="/costlist">Cost List</StyledNavLink></Button>
                <Button><StyledNavLink to="/sqlconnect">SqlConnect</StyledNavLink></Button>
                <Button><StyledNavLink to="/counterapp">CounterApp</StyledNavLink></Button>
                <Button><StyledNavLink to="/todolistapp">TodolistApp</StyledNavLink></Button>
                <Button><StyledNavLink to="/queueapp">QueueApp</StyledNavLink></Button>
                <Button><StyledNavLink to="/rating">Rating</StyledNavLink></Button>
                <Button><StyledNavLink to="/onoff">OnOff</StyledNavLink></Button>

            </Projects>
            <Outlet/>
        </>
    )
}
const Projects = styled.div`

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 30px;
  justify-content: space-between;

`


const StyledNavLink = styled(NavLink)`
  text-decoration: none;
  padding: 1rem;
  border: lightgreen solid;
  margin: 10px;


  &:hover {
    background-color: lightgreen;
  }

  &.active {
    background-color: lightgreen;
    border-radius: 20px;
  }

  &.active:hover {

    border-radius: 20px;
  }
`