import {Todolist} from "./Todolist";
import React, {useEffect, useState} from 'react';
import {v1} from "uuid";
import styled from "styled-components";
import {TypeData} from "./TodolistApp";
import {Paper} from "@mui/material";

export type TaskType = {

    id: string;
    title: string;
    isDone: boolean;
};
export type PropsType = {

    title: string
    onRemoveTodolist: () => void
    number: number
    tasks: any
    setTasks: any
}

function TodolistComponent(props: PropsType) {
    const addNewItem = (title: string) => {
        props.setTasks([...props.tasks, {id: v1(), title, isDone: false}])
    }

    function removeTask(id: string) {
        let filteredTasks = props.tasks.filter((item: { id: string; }) => item.id !== id);
        props.setTasks(filteredTasks);
    }

    const toggleTaskCheckbox = (id: string) => {
        props.setTasks(props.tasks.map((item: TaskType) => item.id === id ? {...item, isDone: !item.isDone} : item))
    }

    const onChangeTitle = (id: string, newValue: string) => {
        props.setTasks(props.tasks.map((item: TaskType) => item.id === id ? {...item, title: newValue} : item))

    }


    let [filter, setFilter] = useState("all");

    function changeFilter(value: string) {
        setFilter(value);
    }
    let tasks
    if (filter === "all") {
         tasks = (props.tasks.map((item: TaskType) => item))
    }

    if (filter === "active") {
         tasks = (props.tasks.filter((item: TaskType) => item.isDone === false))
    }

    if (filter === "completed") {
         tasks = (props.tasks.filter((item: TaskType) => item.isDone === true))
    }

    return (
        <Wrapper>
            <Paper variant="outlined">
                <Todolist title={props.title}
                          tasks={tasks}
                          removeTask={removeTask}
                          changeFilter={changeFilter}
                          addNewItem={addNewItem}
                          toggleTaskCheckbox={toggleTaskCheckbox}
                          filter={filter}
                          onRemoveTodolist={props.onRemoveTodolist}
                          number={props.number}
                          onChangeTitle={onChangeTitle}
                />
            </Paper>
        </Wrapper>
    );
}

export default TodolistComponent;


const Wrapper = styled.div`
  margin: 5px;


`